<div class="searchBarContainer">
    <div class="searchBar {resultsAvailable && "resultsAvailable"}">
        <div class="iconWrapper">
            <span class="mdi mdi-magnify"></span>
        </div>
        <input type="text" {placeholder} bind:value={value} />
    </div>
    {#if resultsAvailable}
        <div class="searchResults">
            {#each results as result}
                <div class="searchBarResult" on:click={() => clickedOnResult(result)}>
                    <div class="iconWrapper">
                        <span class="mdi mdi-{result.icon}"></span>
                    </div>
                    <div class="textWrapper">
                        <p>{result.name}</p>
                    </div>
                </div>
            {/each}
        </div>
    {/if}
</div>

<script>
// Imports
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var placeholder = "";
export var value = "";
export var results = [];

// Computed
$: resultsAvailable = results.length > 0;

// Functions
function clickedOnResult(result) {
    dispatch("pickedResult", result);
}
</script>

