<div class="checkbox {checked && "checked"}" on:click={toggle}>
    {#if checked}
        <span class="mdi mdi-check"></span>
    {/if}
</div>

<script>
// Props
export var checked = false;
export var handleChecked = true;

// Functions
function toggle() {
    if (handleChecked) checked = !checked;
}
</script>

<style lang="scss">.checkbox {
  height: 14px;
  width: 14px;
  position: relative;
  border: 2px solid var(--foregroundColor);
  border-radius: 3px;
  background-color: transparent;
  transition: 100ms background-color cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer; }
  .checkbox.checked {
    background-color: var(--foregroundColor); }
  .checkbox span {
    display: block;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 14px;
    color: var(--backgroundColor);
    animation: 150ms cubic-bezier(0.4, 0, 0.2, 1) popIn; }

@keyframes popIn {
  0% {
    transform: translate(-50%, -50%) scale(0.7); }
  100% {
    transform: translate(-50%, -50%) scale(1); } }

